import React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import ContributionStats from "../components/contributionstats"
import Careers from "../components/careers"


export const pageQuery = graphql`
  query($path: String!) {
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
      frontmatter {
        title
        lead
      }
    }
  }
`

export default function Template({ data }) {
  const { markdownRemark } = data
  const { frontmatter, html } = markdownRemark
  return (
    <Layout>
      <section className="pt-3 pb-3 bg-primary">
        <div className="container">
          <div class="row justify-content-start my-4 my-md-6">
            <div class="col-lg-9 col-xl-8">
              <h1 class="display-4">{ frontmatter.title }</h1>
              <p class="lead mb-0">{ frontmatter.lead }</p>
            </div>
          </div>
          <ContributionStats />
        </div>
      </section>
      <section className="pt-3 pb-6 bg-body">
        <div className="container">
          <div class="row justify-content-center">
            <div class="col-xl-7 col-lg-8 col-md-10 col-11">
              <article class="article">
                <div
                  className="static-page-content"
                  dangerouslySetInnerHTML={{ __html: html }}
                />
              </article>
            </div>
          </div>
        </div>
      </section>
      <Careers />
    </Layout>
  )
}

