import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import numeral from "numeral"

export default () => {
  const data = useStaticQuery(
    graphql`
      query {
        contributionstatsToml {
          contributors
          projects
          commits
        }
      }
    `
  )

  const stats = data.contributionstatsToml

  return (
  <div className="row justify-content-start mb-5">
    <div className="col-3 mr-2 mb-lg-0">
      <span className="display-4 text-dark d-block">{ numeral(stats.contributors).format(0,0) }</span>
      <span className="h6 text-body">Contributors</span>
    </div>
    <div className="col-3 mr-2 mb-lg-0">
      <span className="display-4 text-dark d-block">{ numeral(stats.projects).format(0,0) }</span>
      <span className="h6 text-body">Projects</span>
    </div>
    <div className="col-3 mr-2 mb-lg-0">
      <span className="display-4 text-dark d-block">{ numeral(stats.commits).format(0,0) }</span>
      <span className="h6 text-body">Commits</span>
    </div>
  </div>
)}